::-webkit-scrollbar-track {
	background-color: $scrollbar-track;
}

.scrollbar::-webkit-scrollbar {
	width: 0;
	background-color: $scrollbar-track;
}

::-webkit-scrollbar-thumb {
	background-color: $scrollbar;
	border-radius: 10px;
}

.scrollbar--visible::-webkit-scrollbar {
	width: 6px;
}